import { useState } from "react";
import "../../App.scss"
import GoToTop from "../../top";
import { useForm } from "react-hook-form";
import useWeb3Forms from "@web3forms/react";

function Contact() {


    const {
        register,
        handleSubmit,
        reset,
        watch,
        control,
        setValue,
        formState: { errors, isSubmitSuccessful, isSubmitting },
    } = useForm({
        mode: "onTouched",
    });
    const [isSuccess, setIsSuccess] = useState(false);
    const [message, setMessage] = useState(false);

    // Please update the Access Key in the .env
    const apiKey = process.env.PUBLIC_ACCESS_KEY || "e9ee9eda-6355-4e87-baca-56d2f7d900f9";

    const { submit: onSubmit } = useWeb3Forms({
        access_key: apiKey,
        settings: {
            from_name: "Chem",
            subject: "New Message Chemictionary",
        },
        onSuccess: (msg, data) => {
            setIsSuccess(true);
            setMessage(msg);

            reset();
        },
        onError: (msg, data) => {
            setIsSuccess(false);
            setMessage(msg);

        },
    });

    const clearMsg = () => {
        setTimeout(() => {
            document.getElementById("sent-message-one").style.display = "none";
        }, 5000)
    }
    const clearMsgTwo = () => {
        setTimeout(() => {
            document.getElementById("sent-message-two").style.display = "none";
        }, 5000)
    }




    return (
        <div className="Categories">
            <div className="contact-box pr ">
                <p className="head">
                    Contact Us
                </p>
                <p>We are open for feedbacks and suggestions</p>
                <form onSubmit={handleSubmit(onSubmit)} className="contact-form">


                    <input
                        type="text"
                        placeholder="Full Name"
                        autoComplete="false"
                        required
                        className="contact-inputs"
                        {...register("name")}
                    />



                    <input
                        id="email_address"
                        type="email"
                        placeholder="Email Address"
                        name="email"
                        autoComplete="false"
                        required
                        className="contact-inputs"
                        {...register("email")}
                    />



                    <textarea
                        name="message"
                        placeholder="Your Message"
                        required
                        className="contact-inputs"
                        {...register("message")}
                    />


                    <div className="sub-buttons">
                        <input
                            type="submit"
                            value="Submit"
                            className="submitButton sb"
                        />
                        <input
                            type="reset"
                            value="Reset"
                            className="submitButton rs"
                        />
                    </div>



                </form>
                {isSubmitSuccessful && isSuccess && (
                    <>
                        <p className="sub-search" id="sent-message-one">
                            Message sent successfully
                        </p>
                        {
                            clearMsg()

                        }

                    </>

                )}
                {isSubmitSuccessful && !isSuccess && (
                    <>
                        <p className="sub-search" id="sent-message-two">
                            Something went wrong. Please try again.
                        </p>
                        {
                            clearMsgTwo()

                        }
                    </>
                )}
                {/* <p className="or" >OR</p> */}
            </div>



            <GoToTop />


        </div>

    )
}


export default Contact;