import "../../App.scss"
import reactions from "./reactions.png"
import keyterms from "./keyterms.png"
import concepts from "./concepts.png"
import conversions from "./conversions.png"
import compounds from "./compounds.png"
import trends from "./trends.png"
import periodic from "./periodictable.png"
import keypoints from "./keypoints.png"
import practicals from "./practicals.png"
import competitive from "./competition.png"
import formula from "./formula.png"
import ncert from "./ncert.png"
import inspirations from "./inspirations.png"
import GoToTop from "../../top"
import { Link } from "react-router-dom"

function Categories() {

    const cats = [
        {
            "name": "Reactions",
            "src": reactions,
            "link": "/categories/reactions"
        },
        {
            "name": "Keyterms",
            "src": keyterms
        },
        {
            "name": "Concepts",
            "src": concepts
        },
        {
            "name": "Conversions",
            "src": conversions
        },
        {
            "name": "Compounds",
            "src": compounds
        },
        {
            "name": "Trends (I.C)",
            "src": trends,
            "link": "/categories/trends"
        },
        {
            "name": "Periodic Table",
            "src": periodic
        },
        {
            "name": "Keypoints",
            "src": keypoints
        },
        {
            "name": "Practicals",
            "src": practicals
        },
        {
            "name": "Competitive Corner",
            "src": competitive
        },
        {
            "name": "Formulaes",
            "src": formula
        },
        {
            "name": "NCERT Solutions",
            "src": ncert
        },
        {
            "name": "Inspirations",
            "src": inspirations
        },

    ]


    return (
        <div className="Categories">
            <div className="category-box ct">
                <p className="head">
                    Choose what you want to study
                </p>
                <div className="cats">
                    {
                        cats.map((c) => (

                            <div className="category ">
                                <Link to={c.link} >
                                    <div>
                                        <img className="catImg" src={c.src} />
                                    </div>
                                </Link>
                                <Link to={c.link} >
                                    <div>
                                        <p className="catName" > {c.name} </p>
                                    </div>
                                </Link>

                            </div>


                        ))
                    }
                </div>
            </div>
            <GoToTop />
        </div>
    )
}

export default Categories;