import "../../../App.scss"
import s from "./s.png"
import p from "./p.png"
import d from "./d.png"
import f from "./f.png"
import GoToTop from "../../../top"

function Trends() {

    const cats = [
        {
            "name": "S Block",
            "src": s
        },
        {
            "name": "P Block",
            "src": p
        },
        {
            "name": "D Block",
            "src": d
        },
        {
            "name": "F Block",
            "src": f
        },
     

    ]


    return (
        <div className="Categories">
            <div className="category-box">
                <p className="head">
                   Reactions
                </p>
                <div className="cats">
                    {
                        cats.map((c) => (
                            <div className="category ct">
                                <div>
                                    <img className="catImg" src={c.src} />
                                </div>
                                <div>
                                    <p className="catName" > {c.name} </p>
                                </div>
                            </div>
                        ))
                    }
                </div>
            </div>
            <GoToTop />
        </div>
    )
}

export default Trends;