import "../../../../App.scss"
import glass from "./search.png"
import cross from "./close.png"
import { useEffect, useState } from "react"

function Organic() {

    const [searchValue, setSearchValue] = useState("")

    useEffect(() => {
        if (searchValue != '') {
            document.getElementById("cross").style.display = "flex"
        }
        else if(searchValue == ''){
            document.getElementById("cross").style.display = "none"
        }
    },[searchValue])

    return (
        <div className="Categories">
            <div className="organic-box">
                <p className="head">
                    Organic Reactions
                </p>
                <div className="search-bar-box">
                    <img className="glass" src={glass} />
                    <input type="text" value={searchValue} onChange={(e)=>{setSearchValue(e.target.value)}} className="search-bar" placeholder="Search here" />
                    <img onClick={()=>{setSearchValue("")}} id="cross" className="cross" src={cross} />
                </div>
            </div>
        </div>
    )
}

export default Organic;