import './App.scss';
import Home from './components/home/home';
import Nav from './components/nav/nav';
import { Route, Routes, useLocation } from 'react-router-dom';
import Categories from './components/categories/categories';
import { useEffect, useState } from 'react';
import upp from "./up.png"
import Reactions from './components/categories/reactions/reactions';
import Trends from './components/categories/trends/trends';
import Contact from './components/contact/contact';
import Footer from './components/footer/footer';
import Community from './components/community/community';
import Message from './components/message/message';
import Pricing from './components/pricing/pricing';
import Login from './components/login/login';
import SignUp from './components/signup/signup';
import PrivacyT from './components/privacy/privacy';
import Terms from './components/terms/terms';
import Refund from './components/refund/refund';
import Organic from './components/categories/reactions/organic/organic';


function App() {

  const reveal = () => {

    var reveals = document.querySelectorAll(".pr");


    for (var i = 0; i < reveals.length; i++) {
      var windowHeight = window.innerHeight;
      var elementTop = reveals[i].getBoundingClientRect().top;
      var elementVisible = 120;

      if (elementTop < windowHeight - elementVisible) {
        reveals[i].classList.add("active");
      } else {
        reveals[i].classList.remove("active");
      }
    }


  }
  setInterval(reveal, 1);

  const revealTwo = () => {

    var reveals = document.querySelectorAll(".ct");


    for (var i = 0; i < reveals.length; i++) {
      var windowHeight = window.innerHeight;
      var elementTop = reveals[i].getBoundingClientRect().top;
      var elementVisible = 120;

      if (elementTop < windowHeight - elementVisible) {
        reveals[i].classList.add("active");
      } else {
        reveals[i].classList.remove("active");
      }
    }


  }
  setInterval(revealTwo, 1);

  const revealHome = () => {

    var reveals = document.querySelectorAll(".ht");


    for (var i = 0; i < reveals.length; i++) {
      var windowHeight = window.innerHeight;
      var elementTop = reveals[i].getBoundingClientRect().top;
      var elementVisible = 120;

      if (elementTop < windowHeight - elementVisible) {
        reveals[i].classList.add("active");
      } else {
        reveals[i].classList.remove("active");
      }
    }


  }
  setInterval(revealHome, 1);


  const [showScroll, setShowScroll] = useState(false)


  useEffect(() => {
    const handleScroll = () => {
      // Your scroll-related logic or function call here
      if (window.scrollY > 10) {
        setShowScroll(true)
      }
      else if (window.scrollY < 10) {
        setShowScroll(false)
      }
    };

    // Attach the event listener when the component mounts
    window.addEventListener('scroll', handleScroll);

    // Detach the event listener when the component unmounts
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  const clickScroll = (e) => {
    e.preventDefault()


    window.scrollTo(0, 0);
  }


  const location = useLocation()
  const currentPath = location.pathname;
  const isHome = currentPath == "/"

  return (
    <div id='appMain' className="App">
      {
        !isHome ? <Nav /> : null
      }
      <Routes>
        <Route path='/' element={<Home />} />
        <Route path='/categories' element={<Categories />} />
        <Route path='/login' element={<Login />} />
        <Route path='/signup' element={<SignUp />} />
        <Route path='/community' element={<Community />} />
        <Route path='/reactions/organic' element={<Organic />} />
        <Route path='/our-message' element={<Message />} />
        <Route path='/contact' element={<Contact />} />
        <Route path='/terms' element={<Terms />} />
        <Route path='/refund-policy' element={<Refund />} />
        <Route path='/privacy-policy' element={<PrivacyT />} />
        <Route path='/pricing' element={<Pricing />} />
        <Route path='/categories/reactions' element={<Reactions />} />
        <Route path='/categories/trends' element={<Trends />} />
      </Routes>

      <Footer />

      {
        showScroll
          ? <div id="scrollToTopButton" className="scroll" onClick={clickScroll} >
            <img className='upp' src={upp} />
          </div>
          : null
      }
    </div>
  );
}

export default App;
