import "../../../App.scss"
import reactImg from "./react.png"
import inImg from "./inorgan.png"
import GoToTop from "../../../top"
import { Link } from "react-router-dom"

function Reactions() {

    const cats = [
        {
            "name": "Organic Chemistry",
            "src": reactImg,
            "link": "/reactions/organic"
        },
        {
            "name": "Inorganic Chemistry",
            "src": inImg
        },


    ]


    return (
        <div className="Categories">
            <div className="category-box ct">
                <p className="head">
                    Reactions
                </p>
                <div className="cats">
                    {
                        cats.map((c) => (
                            <div className="category ">
                                <Link to={c.link} >
                                    <div>
                                        <img className="catImg" src={c.src} />
                                    </div>
                                </Link>
                                <Link to={c.link} >
                                    <div>
                                        <p className="catName" > {c.name} </p>
                                    </div>
                                </Link>

                            </div>
                        ))
                    }
                </div>
            </div>
            <GoToTop />
        </div>
    )
}

export default Reactions;