import { Link } from "react-router-dom";
import "../../App.scss"

function SignUp() {


    


    return (
        <div className="Categories">
            <div className="login-box pr">
                <p className="login-logo">Chemictionary</p>

                <form className="contact-form">
                    {/* <h2>Sign Up</h2> */}
                    <input
                        type="text"
                        placeholder="Full Name"
                        autoComplete="false"
                        required
                        className="contact-inputs"

                    />
                    <input
                        type="email"
                        placeholder="Email"
                        autoComplete="false"
                        required
                        className="contact-inputs"

                    />
                    <input
                        type="password"
                        id="passSignup"
                        placeholder="Password"
                        autoComplete="false"
                        required
                        className="contact-inputs"

                    />
                    <input
                        type="password"
                        placeholder="Re-enter password"
                        autoComplete="false"
                        required
                        className="contact-inputs"

                    />


                    <input
                        type="submit"
                        value="Sign Up"
                        className="submitButton sb"
                    />



                    {/* <div className="sub-buttons">
                        <input
                            type="submit"
                            value="Submit"
                            className="submitButton sb"
                        />
                        <input
                            type="reset"
                            value="Reset"
                            className="submitButton rs"
                        />
                    </div> */}

                </form>
                <p className="or" >OR</p>
                <button className="google-btn" >
                    Continue with google
                </button>

                <div className="create-account-div">
                    <p className="dont-have">Already have an account?</p>

                    <Link to="/login">
                        <p className="sign-up-line" >Log In</p>
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default SignUp;