import { Link } from "react-router-dom";
import "../../App.scss"

function Login() {
    return (
        <div className="Categories">
            <div className="login-box pr">
                <p className="login-logo">Chemictionary</p>

                <form className="contact-form">
                    {/* <h2>Log In</h2> */}

                    <input
                        type="email"
                        placeholder="Email"
                        autoComplete="false"
                        required
                        className="contact-inputs"

                    />
                    <input
                        type="password"
                        placeholder="Password"
                        autoComplete="false"
                        required
                        className="contact-inputs"

                    />

                    <input
                        type="submit"
                        value="Log In"
                        className="submitButton sb"
                    />
                    {/* <div className="sub-buttons">
                        <input
                            type="submit"
                            value="Submit"
                            className="submitButton sb"
                        />
                        <input
                            type="reset"
                            value="Reset"
                            className="submitButton rs"
                        />
                    </div> */}

                </form>
                <p className="or" >OR</p>
                <button className="google-btn" >
                    Continue with google
                </button>
                <div className="create-account-div">
                    <p className="dont-have">Don't have an account?</p>
                    <Link to="/signup">
                        <p className="sign-up-line" >Sign Up</p>
                    </Link>
                </div>
            </div>
        </div>
    )
}

export default Login;