import { Link, useLocation } from "react-router-dom";
import "../../App.scss"
import logo from "./logo.png"
import prem from "./premium.png"
import user from "./user.png"

function Nav() {

	const location = useLocation()
	const currentPath = location.pathname;
	const isHome = currentPath == "/"
	const isCat = currentPath == "/categories"
	const isComm = currentPath == "/community"
	const isMsg = currentPath == "/our-message"
	const isPricng = currentPath == "/pricing"
	const isContact = currentPath == "/contact"
	const isSignup = currentPath == "/signup"
	const isLogin = currentPath == "/login"

	return (
		<div className={isHome ? "Nav NavHome" : "Nav"}>
			<div className="logo-container">
				<Link to='/'>
					<img className="logo" src={logo} />
				</Link>
				{/* <p className={isHome ? "login-logo home-login-logo" : "login-logo"} >Chemictionary</p> */}
			</div>

			<div className="menu">
				<Link to='/categories'>
					<p className={isHome ? "menu-item menu-item-home drp" : isCat ? "menu-item active-menu-item drp" : "menu-item drp"}>
						Categories
						{/* <div className="drpdwn " >
							<p className={isHome ? "drpItem drpItemHome" : "drpItem"} >Reactions</p>
							<p className={isHome ? "drpItem drpItemHome" : "drpItem"} >Keyterms</p>
							<p className={isHome ? "drpItem drpItemHome" : "drpItem"} >Concepts</p>
							<p className={isHome ? "drpItem drpItemHome" : "drpItem"} >Community</p>
						</div> */}
					</p>
				</Link>
				<Link to='/community'>
					<p className={isHome ? "menu-item menu-item-home" : isComm ? "menu-item active-menu-item" : "menu-item"}>
						Community
					</p>
				</Link>
				<Link to='/our-message'>
					<p className={isHome ? "menu-item menu-item-home" : isMsg ? "menu-item active-menu-item" : "menu-item"}>
						Our Message
					</p>
				</Link>
				<Link to='/contact'>
					<p className={isHome ? "menu-item menu-item-home" : isContact ? "menu-item active-menu-item" : "menu-item"}>
						Contact
					</p>
				</Link>
				<Link to='/pricing'>
					{/* <img className="prem" src={prem} /> */}
					<p className={isHome ? "menu-item menu-item-home" : isPricng ? "menu-item active-menu-item" : "menu-item"}>
						Pricing
					</p>
				</Link>
				<Link to='/signup'>
					<p className={isHome ? "menu-item menu-item-home" : isSignup ? "menu-item active-menu-item" : "menu-item"}>
						Sign Up
					</p>
				</Link>
				<Link to='/login'>
					<p className={isHome ? "menu-item menu-item-home" : isLogin ? "menu-item active-menu-item" : "menu-item"}>
						Login
					</p>
				</Link>
				{/* <Link to='/profile'>
					<img className="prem" src={user} />
				</Link> */}
			</div>

		</div>
	)
}
export default Nav;