import { useEffect } from "react";
import "../../App.scss"
import GoToTop from "../../top";

function Pricing() {



    useEffect(() => {
        const rzpPaymentForm = document.getElementById("rzp_payment_form");

        if (!rzpPaymentForm.hasChildNodes()) {

            const script = document.createElement("script");
            script.src = "https://checkout.razorpay.com/v1/payment-button.js";
            script.async = true;
            script.dataset.payment_button_id = "pl_NI7c6V5gId7cBA";
            rzpPaymentForm.appendChild(script);

        }

    });

    


    return (
        <div className="Categories">
            <div className="pricing-box ct">
                <p className="head ">
                    Choose that fits you the best
                </p>
                <div className="pricing-cards ">
                    <div className="price-box c1">
                        <div className="pricing-card-head">
                            <p className="pricing-head">Trial</p>
                            <p className="pricing-dets">3 days</p>
                            <div className="benefits">
                                <p className="benefit">Access of each and everything</p>
                                <p className="benefit">Save content in your collection</p>
                                <p className="benefit">Doubt solving session (chat)</p>

                            </div>
                        </div>
                        {/* <div className="pricing-buttons">
                            <button className="pricing-button trial">Buy now</button>
                        </div> */}
                    </div>
                    <div className="price-box c2">
                        <div className="pricing-card-head">
                            <p className="pricing-head">Gold</p>
                            <p className="pricing-dets">₹300/month</p>
                            <div className="benefits">
                                <p className="benefit">Access of each and everything</p>
                                <p className="benefit">Save content in your collection</p>
                                {/* <p className="benefit cut">Doubt solving session (chat)</p> */}
                            </div>
                        </div>
                        <div className="pricing-buttons">
                            {/* <button className="pricing-button gold">Buy now</button> */}
                            <center> <form id="rzp_payment_form"></form></center>
                        </div>
                    </div>
                    <div className="price-box c3">
                        <div className="pricing-card-head">
                            <p className="pricing-head">Platinum</p>
                            <p className="pricing-dets">₹500/month</p>
                            <div className="benefits">
                                <p className="benefit">Access of each and everything</p>
                                <p className="benefit">Save content in your collection</p>
                                <p className="benefit">Doubt solving session (chat)</p>
                            </div>
                        </div>
                        <div className="pricing-buttons">
                            <button className="pricing-button platinum">Buy now</button>
                        </div>
                    </div>
                </div>
            </div>
            <GoToTop />
        </div>
    )
}

export default Pricing;